import { Injectable } from '@angular/core';
import { Person } from '../models/person';

@Injectable()
export class UtilsService
{
  constructor() { 

  }


  public removeAll(list:any[], items: any) : void
  {
    for(var i=0;i<items.length;i++)
    {
      var item=items[i];
      this.removeItem(list, item);
    }
  }

  public removeItem(list:any[], item: any) : void
  {
    var index = list.indexOf(item, 0);
    if (index > -1) {
      list=list.splice(index, 1);
    }
  }

  public isIn(str:string, list:string[]): boolean
  {
      for(var i=0; i<list.length; i++)
      {
          if (str==list[i])
            return true;
      }

      return false;
  }

  public removeIfEndsWith(str:string, strRemove:string): string
  {
    var result=str;
    if (result.endsWith(strRemove))
      result=result.substring(0, result.length - strRemove.length);

    return result;
  }
}


