import { Component, Input } from '@angular/core';
import { MatDialog, MatSnackBar } from '@angular/material';

import { ParserService } from '../services/ParserService';
import { UtilsService } from '../services/UtilsService';
import { PersonTreeService } from '../services/PersonTreeService';
import { PersonNode } from '../models/PersoneNode';
import { TreeDataSource } from '../models/TreeDataSource';
import { PureExpressionData } from '@angular/core/src/view';

@Component({
  selector: 'chart-component',
  templateUrl: './chart.component.html',
  styleUrls: ['./chart.component.css']
})
export class ChartComponent
{  
    private _treeDataSource: TreeDataSource;

    get treeDataSource(): TreeDataSource
    {
        return this._treeDataSource;
    }

    @Input()
    set treeDataSource(value: TreeDataSource)
    {
        this._treeDataSource=value;
        this.onDatasourceChanged();
    }

    public idName:string="chartBody";
    private d3:any=null;
    private tree:any=null;
    private diagonal:any=null;
    private vis:any=null;
    private m:number=null;
    private w:number=null;
    private h:number=null;
    
    public constructor(private utilsService:UtilsService, private personTreeService:PersonTreeService)
    {
        this.idName="chartBody_" + (Math.floor(Math.random() * 100) + 1);
        var w:any=window;
        this.d3=w.d3;
    }

    public getChilds=(d:PersonNode):PersonNode[] =>
    {
        var childs=d[this.treeDataSource.childsProp];
        return childs;
    }

    public setChilds=(d:PersonNode, childs:PersonNode[]):void =>
    {
        d[this.treeDataSource.childsProp]=childs;
    }

    public prepareNode=(parentNode:PersonNode):void =>
    {
        if (parentNode==null) {
            return;
        }

        delete parentNode.id;
        delete parentNode.x;
        delete parentNode.y;
        delete parentNode.x0;
        delete parentNode.y0;
        delete parentNode.depth;
        delete parentNode._childlist;
                
        var childs=this.getChilds(parentNode);
        if (childs!=null)
        {
            for(var i=0;i<childs.length;i++)
            {
                var child=childs[i];
                this.prepareNode(child);
            }
        }
    }
    

    public onDatasourceChanged() : void
    {
        this.prepareNode(this.treeDataSource.nodeself);

        var m = [20, 20, 20, 220];
        var w = 1280 - m[1] - m[3];
        var h = 600 - m[0] - m[2];
        var i = 0;
            
        this.tree = this.d3.layout
            .tree()
            .children(this.getChilds)
            .size([h, w]);

        this.diagonal = this.d3.svg.diagonal().projection(
            function(d:PersonNode) 
            {
                return [d.y, d.x];
            });
        
        var body=this.d3.select("#" + this.idName);
        //body.enter().append("svg:svg");
        body.html("");

        this.vis = body.append("svg:svg")        
            .attr("width", w + m[1] + m[3])
            .attr("height", h + m[0] + m[2])
            .append("svg:g")
            .attr("transform", "translate(" + m[3] + "," + m[0] + ")");
        
        this.func_d3_json();
    }

    public func_d3_json=() => 
    {
        var root=this.treeDataSource.nodeself;
        root.x0 = this.h / 2;
        root.y0 = 0;
        
        this.update();
    }

    public toggleAll=(d:PersonNode):void =>
    {
        var childs=this.getChilds(d);
        if (childs!=null && childs.length>0) 
        {
            childs.forEach(this.toggleAll);
            this.toggle(d);
        }
    }

    public update() : void 
    {
            var duration = this.d3.event && this.d3.event.altKey ? 5000 : 500;
            
            var root=this.treeDataSource.nodeself;

            // Compute the new tree layout.
            var nodes = this.tree.nodes(root).reverse();
            
            // Normalize for fixed-depth.
            nodes.forEach(function(d:PersonNode) 
            { 
                d.y = d.depth * 180; 
            });
            
            // Update the nodes…
            var i=0;
            var node = this.vis.selectAll("g.node")
                    .data(nodes, function(d:PersonNode) 
                    { 
                        return d.id || (d.id = ++i);
                    });
            
            // Enter any new nodes at the parent's previous position.
            var nodeEnter = node.enter().append("svg:g")
                .attr("class", "node")
                .attr("transform", this.funct_node_enter_append)
                .on("click", this.funct_node_enter_append_click);
            
            nodeEnter.append("svg:circle")
                .attr("r", 20)
                .style("fill", function(d:PersonNode) 
                { 
                    return (d._childlist!=null && d._childlist.length>0)  ? "lightsteelblue" : "#fff"; 
                });
            
            nodeEnter.append('a')
                .attr('xlink:href', function(d:PersonNode) 
                {
                    return d.url;
                })
                .append("svg:text")
                .attr("x", this.func_nodeEnter_append2)
                .attr("dy", ".35em")
                .attr("text-anchor", this.func_nodeEnter_append3)
                .text(this.getNodeTitle)
                .style('fill', function(d:PersonNode) 
                {
                    return d.free ? 'black' : '#999';
                })
                .style("fill-opacity", 1e-6);
            
            nodeEnter.append("svg:title")
                .text(this.getNodeTitle);
            
            // Transition nodes to their new position.
            var nodeUpdate = node.transition()
                .duration(duration)
                .attr("transform", function(d:PersonNode) { 
                    return "translate(" + d.y + "," + d.x + ")"; 
                });
            
            nodeUpdate.select("circle")
                .attr("r", 6)
                .style("fill", function(d:PersonNode) 
                { 
                    return (d._childlist!=null && d._childlist.length>0) ? "lightsteelblue" : "#fff"; 
                });
            
            nodeUpdate.select("text")
                .style("fill-opacity", 1);
            
            // Transition exiting nodes to the parent's new position.
            var nodeExit = node.exit().transition()
                .duration(duration)
                .attr("transform", this.func_node_exit_transition)
                .remove();
            
            nodeExit.select("circle")
                .attr("r", 1e-6);
            
            nodeExit.select("text")
                .style("fill-opacity", 1e-6);
            
            // Update the links…
            var link = this.vis.selectAll("path.link")
                .data(this.tree.links(nodes), 
                function(d) 
                { 
                    return d.target.id; 
                });
            
            // Enter any new links at the parent's previous position.        
            link.enter().insert("svg:path", "g")
                .attr("class", "link")
                .attr("d", this.func_svgpath_diagonal)
                .transition()
                .duration(duration)
                .attr("d", this.diagonal);
            
            // Transition links to their new position.
            link.transition()
                .duration(duration)
                .attr("d", this.diagonal);
            
            // Transition exiting nodes to the parent's new position.
            link.exit().transition()
                .duration(duration)
                .attr("d", this.func_link_exit_transition)
                .remove();
            
            // Stash the old positions for transition.
            nodes.forEach(function(d:PersonNode) 
            {
                d.x0 = d.x;
                d.y0 = d.y;
            });
    }

    public getNodeTitle=(d:PersonNode):string =>
    {
        return d.person.name + " " + d.person.surname;
    }

    public func_nodeEnter_append3=(d:PersonNode):any => 
    { 
        var childs=this.getChilds(d);
        return ((childs!=null && childs.length>0) || (d._childlist!=null && d._childlist.length>0)) ? "end" : "start"; 
    }
    public func_nodeEnter_append2=(d:PersonNode):any => 
    {
        var childs=this.getChilds(d);
        return ((childs!=null && childs.length>0) || (d._childlist!=null && d._childlist.length>0)) ? -10 : 10; 
    }


    public funct_node_enter_append_click=(d:PersonNode):void => 
    {
        this.toggle(d); 
        this.update();
    }    

    public funct_node_enter_append=(d:PersonNode):any => 
    {
        var root=this.treeDataSource.nodeself;
        return "translate(" + root.y0 + "," + root.x0 + ")";
    }

    public func_node_exit_transition=(d:PersonNode):string => 
    {
        var root=this.treeDataSource.nodeself;
        return "translate(" + root.y + "," + root.x + ")";
    }

    public func_link_exit_transition=(d:PersonNode):any => 
    {
        var root=this.treeDataSource.nodeself;
        var o = { x: root.x, y: root.y };
        return this.diagonal({source: o, target: o});
    }

    public func_svgpath_diagonal=(d:PersonNode):any => 
    {
        var root=this.treeDataSource.nodeself;
        var o = { x: root.x0, y: root.y0 };
        return this.diagonal({source: o, target: o});
    }
    
    // Toggle childlist.
    public toggle=(d:PersonNode):void =>
    {
        var childs=this.getChilds(d);
        if (childs) {
            d._childlist = childs;
            this.setChilds(d, null);
        } else 
        {
            this.setChilds(d, d._childlist);
            d._childlist = null;
        }
    }
}


