import { Injectable } from '@angular/core';
import { PersonNode } from '../models/PersoneNode';
import { Person } from '../models/Person';
import { UtilsService } from './UtilsService';

@Injectable()
export class PersonTreeService
{
  constructor(private utilsService: UtilsService) { 

  }

  protected createNode(person: Person): PersonNode
  {
    var type="";
    var isRoot=(person==null);

    if (!isRoot)
    {
      switch(person.degree)
      {
        case "Kendisi": type="self"; break;
        case "Babası": type="father"; break;
        case "Annesi": type="mother"; break;
        case "Oğlu": type="son"; break;
        case "Kızı": type="doughter"; break;
        default: type="other"; break;
      }
    }

    var node:PersonNode=
    {
      type: type,
      person: person,
      childlist:[],
      parentlist: [],
    };

    return node;
  }
  
  public getSelfNode(list: Person[]): PersonNode
  {
      var person=null;
      for(var i=0;list.length;i++)
      {
        var item=list[i];
        if (item.degreeCode=="S")
        {
          person=item;
          break;
        }
      }

      if (person==null)
        throw new Error("Kendisi türünde bir kayıt bulunamadı!");

      var node=this.createNode(person);
      return node;
  }

  public generate(list: Person[]): PersonNode
  {      
      // collect
      //var nodeRoot:PersonNode=this.createNode(null);
      var nodeSelf=this.getSelfNode(list);

      var listUnused:Person[]=[].concat(list);

      // childs
      this.generateChilds(nodeSelf, listUnused);

      // parents
      this.generateParents(nodeSelf, listUnused);

      return nodeSelf;
  }


  public prepareCodes(list: Person[])
  {
    for(var i=0;i<list.length;i++)
    {
      var item=list[i];
      var degreeCode=this.getRelativeCodeFromTextAll(item.degree);
      item.degreeCode=degreeCode;
    }
  }

  
  public generateChilds(parentNode:PersonNode, listUnused: Person[]) : void
  {
    var parentCode=((parentNode.person==null || parentNode.person.degreeCode=="S")? "": parentNode.person.degreeCode);
    var newRelativeCodeExtensions=["O", "K"];
        
    var listPerson:Person[]=[];
    for(var i=0;i<newRelativeCodeExtensions.length;i++)
    {
      var newRelativeCodeExtension=newRelativeCodeExtensions[i];
      
      var newCode=parentCode + newRelativeCodeExtension;
      var listPersonTemp=this.getByRelativeFullCode(listUnused, newCode);
      if (listPersonTemp!=null) {
          this.utilsService.removeAll(listUnused, listPersonTemp);
          listPerson=listPerson.concat(listPersonTemp);
      }
    }

    // add relations
    for(let item of listPerson)
    {
      var node=this.createNode(item);      
      parentNode.childlist.push(node);
      node.parentlist.push(parentNode);

      this.generateChilds(node, listUnused);
    }
  }


  public generateParents(parentNode:PersonNode, listUnused:Person[]): void
  {
    var parentCode=((parentNode.person==null || parentNode.person.degreeCode=="S")? "": parentNode.person.degreeCode);
    var newRelativeCodeExtensions=["A", "B"];
        
    var listPerson:Person[]=[];
    for(var i=0;i<newRelativeCodeExtensions.length;i++)
    {
      var newRelativeCodeExtension=newRelativeCodeExtensions[i];
      
      var newCode=parentCode + newRelativeCodeExtension;
      var listPersonTemp=this.getByRelativeFullCode(listUnused, newCode);
      if (listPersonTemp!=null)
      {
          this.utilsService.removeAll(listUnused, listPersonTemp);
          listPerson=listPerson.concat(listPersonTemp);          
      }
    }

    // add relations
    for(let item of listPerson)
    {
      var node=this.createNode(item);
      parentNode.parentlist.push(node);
      node.childlist.push(parentNode);

      this.generateParents(node, listUnused);
    }
  }

  
  // OKOOK => Oğlunu oğlunun kızı
  // BAB => Oğlunu oğlunun kızı

  protected getByRelativeFullCode(list: Person[], degreeCodeFull:string): Person[]
  {
    var results:Person[]=[];
    for(var i=0;i<list.length;i++)
    {
      var item=list[i];
      if (item.degreeCode==degreeCodeFull)
        results.push(item);
    }

    return results;
  }

  protected hasRelativeCodes(list: Person[], relativeCodes:string[]): boolean
  {
      var relativeNames=this.getRelativeNames(relativeCodes);
      for(var i=0; list.length; i++)
      {
        var item=list[i];
        if (this.utilsService.isIn(item.degree, relativeNames))
          return true;
      }

      return false;
  }

  protected getRelativeNames(relativeCodes:string[]): string[]
  {
      var relativeNames:string[]=[];
      for(var i=0; relativeCodes.length; i++)
      {
        var relativeCode=relativeCodes[i];
        var relativeName=this.getRelativeFullText(relativeCode);
        relativeNames.push(relativeName);
      }

      return relativeNames;
  }  

  protected getNextRelativeCodes(codes: string): string[]
  {
    if (codes==null || codes=="")
      throw new Error("codes cannot be NULL!");

    var result:string[]=[];
    var lastCode=codes.substring(codes.length-1, 1);

    switch(lastCode)
    {
      case "O":        
      case "K":
        result.push(codes+"O");
        result.push(codes+"K");
        break;

      case "A":        
      case "B":
        result.push(codes+"A");
        result.push(codes+"B");
        break;

      default:
        throw new Error("Unsupported code! code: " + lastCode);
    }

    return result;
  }

  protected getRelativeFullText(codes: string): string
  {
     var result="";
     for(var i=0;i<codes.length;i++)
     {
        var codeSingle=codes[i];
        var isLast=(i<(codes.length-1));
        var text=this.getRelativeFullTextSingle(codeSingle, isLast);
        result+=text + " ";
     }

     result=this.utilsService.removeIfEndsWith(result, " ");
     return result;
  }

  protected getRelativeFullTextSingle(codeSingle: string, isLast: boolean): string
  {
     var result="";
     switch(codeSingle)
     {
        case "A": result=(isLast? "Annesi": "Annesinin"); break;
        case "B": result=(isLast? "Babası": "Babasının"); break;
        case "O": result=(isLast? "Oğlu": "Oğlunun"); break;
        case "K": result=(isLast? "Kızı": "Kızının"); break;
        default:
          throw new Error("Bilinmeyen kod! codeSingle: " + codeSingle);
     }

     return result;
  }

  protected getRelativeCodeFromTextAll(textAll: string): string
  {
     var codes:string="";
     var parts=textAll.split(" ");
     for(var i=0;i<parts.length;i++)
     {
        var part=parts[i];
        var code=this.getRelativeCodeFromTextSingle(part);
        codes+=code;
     }

     return codes;
  }

  protected getRelativeCodeFromTextSingle(textSingle: string): string
  {
     var result="";
     switch(textSingle)
     {
        case "Annesi":
        case "Annesinin":
          result="A"; 
          break;

        case "Babası":
        case "Babasının":
          result="B"; 
          break;
          
        case "Oğlu":
        case "Oğlunun":
          result="O"; 
          break;             

        case "Kızı":
        case "Kızının":
          result="K"; 
          break;   

        case "Kendisi":
          result="S"; 
          break;
            
        default:
          throw new Error("Bilinmeyen kod! textSingle: " + textSingle);
     }

     return result;
  }
}


