import { Component } from '@angular/core';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ParserService } from './services/ParserService';
import { Person } from './models/person';
import { Column } from './models/Column';
import { UtilsService } from './services/UtilsService';
import { PersonTreeService } from './services/PersonTreeService';
import { PersonNode } from './models/PersoneNode';
import { TreeDataSource } from './models/TreeDataSource';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent 
{  
  public txt: string="";

  public items: Person[]= [];

  public columnNames: string[];
  public columns: Column[] = [ 
    { name: "no", title: "No" },     
    { name: "degree", title: "Yakınlık" }, 
    { name: "degreeCode", title: "Yakınlık Kodu" }, 
    { name: "gender", title: "Cinsiyet" },     
    { name: "name", title: "Ad"}, 
    { name: "surname", title: "Soyad" },
  ];

  public nodeSelf: PersonNode= {
    type: "",
    person: null,
    parentlist: [],
    childlist: []
  };

  public isLoaded:boolean=false;
  public treeDataSource_childs:TreeDataSource =  {   childsProp:"childlist", nodeself:this.nodeSelf };
  public treeDataSource_parents:TreeDataSource =  {   childsProp:"parentlist", nodeself:this.nodeSelf };

  constructor(private parserService: ParserService, private utilsService:UtilsService, private personTreeService: PersonTreeService) 
  {
    this.columnNames = this.getColNames(this.columns);
  }

  protected getColNames(cols: Column[]): string[] 
  {
    var list: string[] = [];

    if (cols!=null)
    {    
        for(var i=0;i<cols.length;i++)   
        {
            var col=cols[i];
            list.push(col.name);
        }
    }
     
    return list;
  }
  

  public parse() 
  {    
    var items: Person[]= this.parserService.parse(this.txt);
   
    this.items=items;
    this.isLoaded=true;
    
    this.nodeSelf=this.personTreeService.generate(items);
    this.treeDataSource_childs= {  childsProp:"childlist", nodeself:this.nodeSelf };
    this.treeDataSource_parents= {  childsProp:"parentlist", nodeself:this.nodeSelf };
  }
}



