import { Injectable } from '@angular/core';
import { Person } from '../models/person';
import { PersonTreeService } from './PersonTreeService';
import { UtilsService } from './UtilsService';

@Injectable()
export class ParserService
{
  constructor(private utilsService:UtilsService, private personTreeService: PersonTreeService) 
  { 

  }

  public parse(txt: string): Person[]
  {
        var items: Person[]= [];

        var linesTemp=txt.split("\n");
        var lines=this.normalizeLines(linesTemp);
    
        for(var i=0;i<lines.length;i++)
        {
            var line=lines[i];
    
            var cols=line.split("\t");
    
            var person:Person={
              no: cols[0],
              gender: cols[1],
              degree: cols[2],
              name: cols[3],
              surname: cols[4],
              father: cols[5],
              mother: cols[6],
    
              birtplace: cols[7],
              city: cols[8],
    
              maritalstatus: cols[10],
              status: cols[11],
            };
    
            items.push(person);
        }

        this.personTreeService.prepareCodes(items);

        return items;
  }

  protected mergeCols(cols:string[]): string
  {
    var str:string ="";
    for(var i=0;i<cols.length;i++)
    {
      var col=cols[i];
      str+=col + "\t";
    }

    return str;
  }

  

  public normalizeLines(lineGroups: string[]): string[]
  {
    var newLines:string[]=[];
    var cols:string[]=[];

    for(var i=0;i<lineGroups.length;i++)
    {      
      var lineGroup=lineGroups[i];
      var groupCols:string[]=lineGroup.split("\t");
      if (groupCols[0]=="Sıra")
        continue;
      
      cols=cols.concat(groupCols);
      if (cols.length>=16)
      {
        var newLine=this.mergeCols(cols);
        newLine=this.utilsService.removeIfEndsWith(newLine, "\t");
        newLines.push(newLine);
        cols=[];
      }
    }

    return newLines;
  }
}


