import { Pipe, PipeTransform } from '@angular/core';
import { Column } from '../models/Column';
 
@Pipe({
  name: 'columnnames',
  pure: false
})
export class ColumnNameListPipePipe  implements PipeTransform 
{
  constructor() { } 
  transform(cols: Column[]): string[] 
  {
    var list: string[] = [];

    if (cols!=null)
    {    
        for(var i=0;i<cols.length;i++)   
        {
            var col=cols[i];
            list.push(col.name);
        }
    }
     
    return list;
  }
}


